import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

// 
const state ={
  News_All:"news",
  news_key:"",           //
  ifstate:"",          //
  card_key:"",
  Second_Acitive:'/',     //
  InitSecodRouter:[],
  Editor_key:""    //add_news/add_notide/change_title
}

//  mutations
const mutations = {
     HandleSecondRouter(state, payload) {
       //alert(payload.firstAct);
       switch (payload.firstAct) {
          case "news":
          state.InitSecodRouter=[{index:"/",name:""},{index:"/notice",name:""}];
          break;
        case "labs":
           state.InitSecodRouter=[{index:"/attack",name:""},{index:"/virtual",name:""},{index:"/domains",name:""},{index:"/yun",name:""}];
        //   alert(payload.firstAct);
           break;
        case "intro":
           state.InitSecodRouter=[{index:"/intro",name:""},{index:"/leaders",name:""},{index:"/environment",name:""}];
           break;
        case "teachers":
            state.InitSecodRouter=[{index:"/teachers",name:""}];
            break;
       case "activities":
            state.InitSecodRouter=[{index:'/activities',name:""},{index:"/happytime",name:""}]
             break;
       }
      // alert(state.InitSecodRouter);
    },
    showUserName(state){
        alert(state.username);
    },
    DECREMENT(state) {
        state.count--
    },
  /*  setTaskId: (state, name) => {
        state.taskid = name
    }*/
}

//  store 
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})



