<template>
  <div class="intro">
    <div id="details" >
       <div id="title" v-for="(item,index) in TitleInOnepage" :key="index">
            <el-button  type="text" @click="details(item.id)" style="padding:5px 0">
              <span style="font-size:15px">{{item.name}}</span>
            </el-button>
          <hr/>
       </div>
       <div class="editNews">
          <el-button icon="el-icon-circle-plus" @click="add"></el-button>
          <el-button icon="el-icon-edit" @click="editNews"></el-button>
      </div>
    </div>
    <br/>
    <div class="block">
    <el-pagination
      @current-change="handleCurrentChange"
      current-page.sync=1
      :page-size="onepage"
      layout="prev, pager, next, jumper"
      :total="length">
    </el-pagination>
  </div>
    <br/>
  </div>
</template>

<script>
import Axios from 'axios'
export default{
  data(){
    return{
    //  AllN:[],  ///
    //  TitleInOnepage:[],//
    //  length:0, //
  //    onepage:0, //
    }
  },
  props:{
    AllN:[],  ///
    TitleInOnepage:[],//
    length:Number, //
    onepage:Number, //
  },
  methods: {
    add(){
      //alert(this.$store.state.News_All);
      if(this.$store.state.News_All=="news")
      {
        //alert("news");
        this.$store.state.Editor_key="add_news";
      }
      else
      {
        this.$store.state.Editor_key="add_notice"
      }
      this.$router.push("/editor");
    },
    editNews(){
      this.$notify({
        title: 'Tips',
        message:"" ,
        type: 'success'
      });
  },
    Init_page(url){
      Axios.get(url)
      .then(function(res){
           var len=res.data.data.length;
           this.length=len;            //
           var tem=[];
           for(var i=0;i<len;i++){
             tem.push({
                 name:res.data.data[i].name,
                 time:res.data.data[i].time,
                 id:res.data.data[i].id
             })
           }
           this.AllN=tem;
           this.TitleInOnepage=tem.slice(0,this.onepage);    //
      }.bind(this))
      .catch(function(){
        console.log("error");
      })
    },
    handleCurrentChange(val) {
       //
       var tem=[];
       tem=this.AllN;
       var start=this.onepage*(val-1);
       var end=this.onepage*val;
       if(end>this.length) end=this.length;
       this.TitleInOnepage=tem.slice(start,end);
     },
    Calcu(){
      //detailsdom
      //
      alert("hi");
    },
    details(id){
    this.$store.state.news_key=id;  //,ididkey
    this.$store.state.Editor_key="change_title"
    this.$router.push('/editor');
},

  }
}
</script>

<style>
.intro{
 position: relative;
 width: 96%;
 min-height: 400px;
}
.intro #details{
  width: 90%;
  position: relative;
  left:5%;
  background:rgba(234,234,234,0.1);
  border-radius:10px;
  height: 400px;
  overflow: hidden;
}
.intro #title{
  width:80%;
  position: relative;
  left:10%;
  overflow: hidden;
  top:10px
}
.block{
float: right;
}

</style>



