<template>
  <div>
  <div class="editor-container">
    <script id="editor" type="text/plain"></script>
  </div>
  <br/>
 <center><div>
  <el-button type="primary" @click="postUEContent()"></el-button>
  <el-button  @click="render_in_editor()"></el-button>
  </div>
</center>
  <br/><br/>
</div>
</template>

<script>
import Axios from 'axios'
import bus from '../assets/eventBus.js'
import{PostDataURL, renderURL}from'../api/httpapi.js'
  export default {
    data () {
      return {
        editor: null,
        key:"", //urlkey
        defaultMsg:"",
        config: {
          toolbars: [
         ['preview','inserttitle', 'fontsize', //
        'paragraph', //
        'simpleupload', //
        'insertimage','link', //
        'emotion', //
        'spechars',
         'forecolor',
         'customstyle'
         ],
         [  'justifyleft', //
            'justifyright', //
            'justifycenter', //
            'justifyjustify'
         ],
      ['bold', 'italic', 'underline', 'superscript', 'subscript', 'removeformat', 'formatmatch', 'blockquote', '|', 'insertorderedlist', 'insertunorderedlist', 'selectall', 'cleardoc']
  ],
           initialFrameWidth :'100%',
           initialFrameHeight:400
      }
    }
  },
    mounted() {
      const _this = this;
      this.editor = UE.getEditor('editor', this.config); // UE
      this.editor.ready(function(){
      // _this.editor.setContent(_this.defaultMsg); // UE
      _this.init();       //OR

      })
    },
    methods: {
      init(){
         //,editor_key;
         var edit_key=this.$store.state.Editor_key;
         switch (edit_key) {
           case "add_news":
             //alert("");
               this.key="add_news";
               break;
           case "add_notice":
               //alert("");
               this.key="add_notice"
               break;
           case "change_title":
               //  alert("/");
               //  alert(this.$store.state.news_key)
               this.key=this.$store.state.news_key;   //
               this.renderHTML(this.key);
               break;
           case "index_imgs_html":
                      this.key="index_imgs_html";
                      this.renderHTML(this.key);
                      break;
          case "rewrite_"+this.$store.state.card_key.name:

                      this.defaultMsg=this.$store.state.card_key.edit;
                      this.key=this.$store.state.Editor_key;
                      this.render_in_editor();
                      break;
          case "rewrite_desc_"+this.$store.state.card_key:
                      this.key="rewrite_desc_"+this.$store.state.card_key;
                      this.renderHTML("desc_"+this.$store.state.card_key);
                      break;
          case "add_leader":
                      this.key=this.$store.state.Editor_key;
                      this.renderHTML(this.key);
                      break;
          case "add_teacher":
                      this.key=this.$store.state.Editor_key;
                      this.renderHTML(this.key);
                      break;
          case "add_jishi":
                      this.key=this.$store.state.Editor_key;
                      this.renderHTML(this.key);
                      break;
        case "add_happy":
                      this.key=this.$store.state.Editor_key;
                      this.renderHTML(this.key);
                      break;
        case "index_imgs_html":
                this.key="index_imgs_html";
                this.renderHTML(this.key);
                break;
        case "intro":
               //
               this.key="intro";
               this.renderHTML(this.key);
               break;
       case "lab_attack":
              //
              this.key="lab_attack";
              alert(this.key);
              this.renderHTML(this.key);
              break;
       case "lab_virtual" :
              //
              this.key="lab_virtual";
              this.renderHTML(this.key);
              break;
      case "lab_cloud":
         //
         this.key="lab_cloud";
         this.renderHTML(this.key);
         break;
      case "lab_domains":
       //
       this.key="lab_domains";
       this.renderHTML(this.key);
       break;
       case "projects":
         //
         this.key="projects";
         this.renderHTML(this.key);
         break;
       case "article":
          //
          this.key="article";
          this.renderHTML(this.key);
          break;
       case "friends":
          //
          this.key="friends";
          this.renderHTML(this.key);
          break;
       case "gains":
           //
           this.key="gains";
           this.renderHTML(this.key);
           break;
       case "patent":
       //
       this.key="patent";
       this.renderHTML(this.key);
       break;
       case "soft":
       //
       this.key="soft";
       this.renderHTML(this.key);
       break;
       case  "union":
            //
            this.key="union";
            this.renderHTML(this.key);
            break;
      case"enviro_imgs_html":
             this.key="enviro_imgs_html";
             this.renderHTML(this.key);
             break;
      default:
               alert("");
        }
      },
      renderHTML(key){
         const _this=this;
         //alert("urlkey:"+key);
         //var renderURL="/api/getdata?key=";
         var content="chushi";
         Axios.get(renderURL+key)
         .then(function(res){
             this.defaultMsg=res.data.HTML;
             //alert(this.defaultMsg);
           }.bind(this))
         .catch(function(){
           console.log("error");
         });
         setTimeout(_this.render_in_editor,500);
         _this.render_in_editor();
  },
      render_in_editor(){
        const_this=this;
         //alert("hi");
        this.editor.ready(function(){
         _this.editor.setContent(_this.defaultMsg); // UE
        })
    },
      postUEContent() {
        let content=this.editor.getContent();       //
        var mydata={"HTML":content};
        //var PostDataURL='/api/postdata?key='
        var KEY=this.key;
        Axios.post(PostDataURL+KEY,mydata)
        .then(function(res){
            if(res.data.state=="SUCCESS")
            {
              alert("");
            }
            else{
              alert(res.data.error);
            }
        })
        .catch(function(){
              console.log("");
            })
     },
},
    destroyed() {
      this.editor.destroy();
    }
  }
</script>

<style>
.editor-container{
      width:80%;
      position: relative;
      left:10%
  }
</style>



